footer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem 2rem;
  text-align: center;
}

footer div {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.socialIcons {
  background: var(--primary);
  color: white;
  font-size: 2rem;
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

footer p {
  font-size: 1.8rem;
  color: var(--black);
}
